.login-card {
  border: 0;
  border-radius: 27.5px;
  box-shadow: 0 10px 30px 0 rgba(172, 168, 168, 0.43);
  overflow: hidden;
}
.login-card-img-login {
  border-radius: 0;
  position: absolute;
  width: 100%;
  margin-left: 40px;
  height: 100%;
  -o-object-fit: fill;
  object-fit: fill;
}
.login-card .card-body-login {
  padding: 85px 130px 60px;
}
@media (max-width: 422px) {
  .login-card .card-body-login {
    padding: 35px 24px;
  }
}
.login-card-description-login {
  color: var(--black);
  margin-bottom: 23px;
}
.login-card form {
  max-width: 326px;
}
.login-card .form-control {
  border: 1px solid #d5dae2;
  padding: 15px 25px;
  margin-bottom: 20px;
  min-height: 45px;
  font-size: 13px;
  line-height: 15;
  font-weight: normal;
}
.login-card .form-control::-webkit-input-placeholder {
  color: #919aa3;
}
.login-card .form-control::-moz-placeholder {
  color: #919aa3;
}
.login-card .form-control:-ms-input-placeholder {
  color: #919aa3;
}
.login-card .form-control::-ms-input-placeholder {
  color: #919aa3;
}
.login-card .form-control::placeholder {
  color: #919aa3;
}
.login-card .login-btn2 {
  padding: 13px 20px 12px;
  background-color: #3e83d1;
  border-radius: 4px;
  font-size: 17px;
  line-height: 20px;
  color: #fff;
  margin-bottom: 24px;
}
.login-card .login-btn2:hover {
  border: 1px solid #000;
  background-color: transparent;
  color: #000;
}
.login-card .forgot-password-link {
  font-size: 14px;
  color: #919aa3;
  margin-bottom: 12px;
}
.login-card-footer-text {
  font-size: 16px;
  color: #0d2366;
  margin-bottom: 60px;
}
@media (max-width: 767px) {
  .login-card-footer-text {
    margin-bottom: 24px;
  }
}
.login-card-footer-nav a {
  font-size: 14px;
  color: #919aa3;
}

@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1100px !important;
  }
}

@media (max-width: 700px) {
  .center, .login-card-description-login {
   text-align: center;
  }
}